import ajax from './ajax'

export const reqCode = (appId,redirect_uri,scope) =>ajax(`https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appId}&redirect_uri=${redirect_uri}&response_type=code&scope=${scope}&state=STATE#wechat_redirect`);
// https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx6ad3262297242b11&redirect_uri=localhost:3000&response_type=code&scope=snsapi_base&state=STATE#wechat_redirect

export const doLogin = ({phone,auto_code,identity,open_id,name}) => ajax('/api/login/do_login',{phone,auto_code,identity,open_id,name});
